@import "mixins";


body, html {
	height: 100%; }

body {
	font-family: 'XLight', sans-serif; }

body, h2, p {
	margin: 0; }

.top {
	width: 100%;
	height: 100px; }

.carousel-cell:before,
.carousel-cell__number {
	display: none !important; }


.carousel {
	// height: 200px
	// max-height: 100% !important
	// min-height: 695px	!important
	// height: 100%
	.carousel-cell {
		background: #ffecb3;
		position: relative;
		@include transition-ease(0.7s); }
	.carousel-cell-h {
		font-weight: 200;
		margin-bottom: 55px;
		font-size: 36px; }
	.flickity-viewport:after {
		content: '';
		display: table;
		clear: both; } }


.carousel--show-several {
	.carousel-cell {
		width: 100% !important;
		height: 100%;
		padding: 80px;
		padding-left: 4%;
		padding-right: 4%; } }


.carousel-cell-container {
	position: relative;
	width: 80%;
	margin: auto;
	font-size: 0;
	letter-spacing: -1px;
	span {
		display: block; } }

.carousel-cell-text-block {
	// vertical-align: top
	vertical-align: bottom;

	letter-spacing: normal;
	position: relative;
	width: 49%;
	font-size: 18px;
	display: inline-block;
	.text-block:nth-child(2) {
		margin-bottom: 20px; }
	.text-block {
		max-width: 380px;
		margin-bottom: 30px;
		@include transition-ease(0.7s);
		.text-block-text {
			line-height: 22px;
			padding-right: 10px; } }
	.text-block-cms {
		margin-bottom: 20px; }
	.text-block-h {
		font-family: 'Regular'; }
	.carousel-cell-logo {
		margin-bottom: 50px; }
	.text-block-link {} }

.device-block {
	vertical-align: bottom;
	width: 50%;
	max-width: 705px;
	height: 100%;
	// position: absolute
	position: relative;
	// bottom: 0
	// right: 4%
	display: inline-block;
	@include transition-ease(0.2s);

	.device-img {
		// width: 50%
		// position: fixed
		position: absolute;
		bottom: 0px;
		display: inline-block;
		text-align: center;
		@include transition-ease(0.2s);
		img {
			width: 95%;
			vertical-align: middle;
			vertical-align: top; } }
	.device-block-desktop {
		max-width: 660px;
		width: 105%;
		right: 0; }
	.device-block-tablet {
		right: 75%;
		max-width: 241px;
		width: 38%; }
	.device-block-mobile {
		right: 103%;
		max-width: 115px;
		width: 18%; } }


.div {
	width: 75%;
	height: 100px;
	background: #000; }




